












































import Vue from 'vue';
import Component from 'vue-class-component';
import { Message } from 'element-ui';
import Router from 'vue-router';
import { State } from 'vuex-class/lib/bindings';
import { initRouter } from '@/router';
import { ElForm } from 'element-ui/types/form';
import { load } from '@/assets/utils/download';

interface LoginInfo {
    password: string;
    username: string;
}

@Component
export default class Login extends Vue {
    public form: LoginInfo = { password: '', username: '' };
    public rules = {
        username: [
            { min: 5, message: '5', trigger: 'change' },
            { required: true, message: '', trigger: 'blur' }
        ],
        password: [
            { min: 6, max: 12, message: '612', trigger: 'change' },
            { required: true, message: '', trigger: 'blur' }
        ]
    };

    public submit() {
        const form = <ElForm>this.$refs.loginForm;
        form.validate((valid: boolean) => {
            if (valid) {
                fetch('/api/admin/login', {
                    body: JSON.stringify(this.form),
                    method: 'POST',
                    headers: {
                        'Content-Type': 'application/json'
                    }
                })
                    .then(res => res.json())
                    .then((res: ResponseData) => {
                        if (res.code === 200) {
                            sessionStorage.setItem('login', '1');
                            return res.resultMap.admin;
                        } else {
                            return Promise.reject({
                                message: ', !'
                            });
                        }
                    })
                    .then((user: string) => {
                        user && sessionStorage.setItem('user', user);
                        initRouter();
                        load('/api/tag/getall', 'tagNo', 'tag');

                        this.$router.push('/');
                    })
                    .catch((e: any) => this.$message.error(e.message))
                    .finally(() => form && form.resetFields());
            }
        });
    }

    public get isVaild() {
        const nameLength = this.form.username.length;
        const passLength = this.form.password.length;

        return nameLength >= 5 && passLength >= 6 && passLength <= 12;
    }
}
