import Vue from 'vue';
import Vuex from 'vuex';
import { BASE_URL } from './constant';

Vue.use(Vuex);

export interface ZoneMode {
    in: number; // 
    out: number; // 
    switch: number; // 
    group: number; // 
    other: number; // 
}

const store = new Vuex.Store({
    state: {
        baseUrl: BASE_URL,
        zoneMode: {
            switch: 1,
            in: 2,
            out: 3,
            group: 4,
            other: 5
        },
        modeDescript: ['', '', '', '', '', ''],
        rootWidth: 0
    },
    mutations: {
        rootWidth(state, width: number) {
            state.rootWidth = width;
        }
    }
});

export default store;
