











import Vue from 'vue';
import Component from 'vue-class-component';
import Header from '../components/layout/Header.vue';
import { Prop } from 'vue-property-decorator';
import { NOTIFY_KEY, RECOVERY } from '@/constant';
import { getIp, getConfig } from '@/assets/utils/util';

@Component({
    components: {
        'app-nav': Header
    }
})
export default class Main extends Vue {
    private readonly delay = 500 / getConfig<number>('SECOND_COUNT', 1);

    public created() {
        this.link();
        this.$event.emit(RECOVERY);
    }

    private link() {
        const ip = getIp();
        if (!ip) {
            return;
        }
        const wsUrl = getConfig<string>(
            'websoket.sundries',
            'ws://#{ip}/alarm'
        );
        const ws = new WebSocket(wsUrl.replace('#{ip}', ip));
        ws.onmessage = (msg: MessageEvent) => {
            let data: Omit<IAlarm, 'id'>;
            try {
                data = JSON.parse(msg.data);
            } catch (e) {
                const arr = msg.data.split('$');
                data = {
                    baseNo: arr[0],
                    tagNo: arr[1],
                    type: +arr[2],
                    alarmTime: +new Date(arr[3]).getTime(),
                    alarmMsg: arr[4]
                };
            }

            if (Date.now() - data.alarmTime <= 1000) {
                setTimeout(
                    () => this.$event.emit(NOTIFY_KEY, data),
                    this.delay
                );
            }
        };
    }
}
